@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
/** Page Setup */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  background-color: #fff;
}

h1,
h2,
h3 {
  margin: 0.5rem 0;
}

h1 {
  font-size: 1.5rem;
}

h2 {
  font-size: 1.2rem;
}

h3 {
  font-size: 1rem;
}

a {
  text-decoration: none;
  color: #fff;
}

.row {
  display: flex;
  justify-content: space-between;
}

.row.center {
  align-items: center;
}

.col-1 {
  flex: 1 1;
}

.col-2 {
  flex: 2 1;
}

.main-page {
  margin-left: 0.5rem;
  margin-right: 10rem;
}

header {
  top: 0;
  left: 0;
  padding: 1rem;
  width: 100%;
  background-color: #1e1e1e;
  height: 5rem;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  z-index: 10;
}

.text-right {
  text-align: right;
}

/** Buttons */
button {
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 0.5rem;
  border: 0.1rem #f15b2d80;
  background-color: #f15b2d;
  width: 100%;
  cursor: pointer;
  color: #fff;
  font-size: 1rem;
}

button.add {
  background-color: #00bbff;
  width: 1.5rem;
}

button.remove {
  background-color: #ff0000;
  width: 1.5rem;
}

.btn-food {
  width: 6rem;
  height: 3rem;
  font-size: 0.8rem;
}

/** Sushi Image */
.main-image {
  margin: auto;
  width: 20%;
}

.main-image img {
  width: 100%;
}

@media (max-width: 1200px) {
  .main-image {
    width: 20rem;
  }
}

@media (max-width: 768px) {
  .header-h1 {
    font-size: 1.4rem;
  }
}

/** Meals & Basket */
.meal {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
}

.description {
  font-style: italic;
}

.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: #f15b2d;
  font-size: 1.25rem;
}

.card {
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
  border-radius: 14px;
  background-color: #fff;
}

.basket {
  margin: auto;
  margin-right: 0.5rem;
  padding: 100rem;
  text-align: left;
  max-width: 45rem;
  width: 90%;
  background-color: #1e1e1e;
  color: #fff;
  border-radius: 14px;
  padding: 4rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 768px) {
  .row {
    display: grid;
  }
  .basket {
    margin: auto;
  }
  .col-1 {
    flex: none;
  }
  .main-page {
    margin: 2rem;
  }
  .text-right {
    text-align: justify;
  }
}

/** About */
.about {
  margin-top: 12rem;
}

.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  justify-content: center;
  align-items: center;
  background-color: #1e1e1e;
  color: #fff;
  border-radius: 14px;
  padding: 2rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
}

.summary h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.week {
  margin-top: 6rem;
  justify-content: center;
  text-align: center;
  font-style: italic;
  color: #1e1e1e;
  line-height: 3rem;
}

.reservations {
  color: rgba(0, 0, 0, 0.6);
}

.thanks {
  color: rgba(0, 0, 0, 0.4);
}

